.redditPost {
  width: 480px;
  background: #464a50;
  color: white;
  padding: 18px;
  text-align: left;
  font-size: 16px;
  border-top: 1px solid white;
  border-bottom: 3px solid grey;
  margin-left: 24px;
}

.redditPost img {
  float:left;
  margin-right: 18px;
}
